import * as THREE from "three";
// 
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
// GSAP
import gsap from "gsap";
// GUI
import * as dat from "dat.gui";

/**
 * DOM
 */
let loadDom = document.createElement("div");
loadDom.style.cssText =
  "width: 100%;height:20px;line-height:20px;text-align:center;font-size:18px;position:absolute;top:10px;right:0;color:white;";
document.body.appendChild(loadDom);

/**
 * 1.
 */
const scene = new THREE.Scene();

/**
 * 2.
 */
// ; (, ,, ;)
const camera = new THREE.PerspectiveCamera(
  75,
  window.innerWidth / window.innerHeight,
  0.1,
  1000
);
// ; (x,y,z);
camera.position.set(0, 0, 10);
// 
scene.add(camera);

/**
 * 3.
 */

let eventCollection = {
  onLoad: function () {
    console.log("");
  },
  onProgress: function (url, num, total) {
    loadDom.innerText = `${((num / total) * 100).toFixed(2)}%`;
  },
  onError: function (err) {
    console.log(err);
  },
};
// 
const loadingManager = new THREE.LoadingManager(
  eventCollection.onLoad,
  eventCollection.onProgress,
  eventCollection.onError
);

const textureLoader = new THREE.TextureLoader(loadingManager);
// 
let texture = textureLoader.load(
  "./textures/fruits.jpg"
  // eventCollection.onLoad,
  // eventCollection.onProgress,
  // eventCollection.onError
);
// 
let alphaTexture = textureLoader.load("./textures/grid1.png");
// 
let aoTexture = textureLoader.load("./textures/grid2.png");
// 
let heightTexture = textureLoader.load("./textures/fruits2.png");
// 
let roughnessTexture = textureLoader.load("./texture/fruits3.png");
// 
let metalnessTexture = textureLoader.load("./texture/fruits2.png");
// 
let normalTexture = textureLoader.load("./texture/fruits2.png");

const cubeGeometry = new THREE.BoxGeometry(1, 1, 1, 100, 100, 100);
const cubeMaterial = new THREE.MeshStandardMaterial({
  color: "#ffff00",
  map: texture,
  transparent: true,
  alphaMap: alphaTexture,
  opacity: 0.8,
  aoMap: aoTexture,
  aoMapIntensity: 0.9,
  displacementMap: heightTexture,
  displacementScale: 0.03,
  roughness: 1,
  roughnessMap: roughnessTexture,
  metalness: 1,
  metalnessMap: metalnessTexture,
  // normalMap: normalTexture,
});
// aoMapuv
cubeGeometry.setAttribute(
  "uv2",
  new THREE.BufferAttribute(cubeGeometry.attributes.uv.array, 2)
);
cubeMaterial.side = THREE.DoubleSide;
const cube = new THREE.Mesh(cubeGeometry, cubeMaterial);
scene.add(cube);

/**
 * 
 */
// ();
// const ambientLight = new THREE.AmbientLight(0xffffff, 0.8);
// scene.add(ambientLight);
// 
const directionalLight = new THREE.DirectionalLight(0xffffff, 0.8);
directionalLight.position.set(10, 10, 10);
scene.add(directionalLight);

/**
 * 4.
 */
// ;
const renderer = new THREE.WebGLRenderer();
// ; (, );
renderer.setSize(window.innerWidth, window.innerHeight);
// webglcanvasbody
document.body.appendChild(renderer.domElement);

/**
 * 5.
 */
// ; (,Canvas);
const control = new OrbitControls(camera, renderer.domElement);
// ,;
control.enableDamping = true;

// ,
renderer.render(scene, camera);

/**
 * 
 */
const axesHelper = new THREE.AxesHelper(5);
scene.add(axesHelper);

/**
 * 
 */
function render() {
  control.update();
  renderer.render(scene, camera);
  // rander;
  requestAnimationFrame(render);
}
render();

/**
 * ,
 */
window.addEventListener("resize", () => {
  // 
  camera.aspect = window.innerWidth / window.innerHeight;
  // 
  camera.updateProjectionMatrix();
  // 
  renderer.setSize(window.innerWidth, window.innerHeight);
  // 
  renderer.setPixelRatio(window.devicePixelRatio);
});

/**
 * 
 */
window.addEventListener("contextmenu", () => {
  // (DOM);
  if (document.fullscreenElement) {
    // 
    document.exitFullscreen();
  } else {
    // 
    renderer.domElement.requestFullscreen();
  }
});
